
<div class="container">
    <div class="container-items">
        <div 
            class="card"
            v-for="card in cards"
            :key="card">
            <div class="title"> {{ card.title }} </div>
            <div class="links">
                <a 
                    v-for="link in card.links"
                    :key="link"
                    class="btn" 
                    :href="link.url"
                    :disabled="link.disabled">{{ link.title }}</a>
            </div>
        </div>
    </div>

</div>
